@charset "utf-8";

@import "../assets/scss/_webfont";
@import "../assets/scss/_element-ui";
@import "../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}

.datePickerInput__popup,
.dateRangePickerInput__popup {
  z-index: 100;
}






























.home {
  margin-right: auto;
  margin-left: auto;
  padding: 300px 100px;
  min-height: 1300px;
  max-width: 1000px;

  .list {
    &:not(:first-child) {
      margin-top: 150px;
    }
  }

  h2 {
    margin-bottom: 20px;
    font-weight: bold;
    font-size: 20px;
  }
}
