@charset "utf-8";

@import "../../../assets/scss/_webfont";
@import "../../../assets/scss/_element-ui";
@import "../../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}

.datePickerInput__popup,
.dateRangePickerInput__popup {
  z-index: 100;
}


































































.calendar-base__title {
  display: flex;
  align-items: center;
  justify-content: center;
  height: $datePickerCellHeight;
  border-bottom: 1px solid $colorBase400;
  text-align: center;
}

.calendar-base__text {
  display: inline-block;
  padding: 5px 10px 0;
  height: $datePickerCellHeight;
}

.calendar-base__body {
  margin-top: 5px;
  width: 100%;
  table-layout: fixed;
}

.calendar-base__cell {
  height: $datePickerCellHeight;
  color: $colorElmentUIGray600;
  vertical-align: middle;
  text-align: center;
  font-size: 12px;
}

.calendar-base__bodyCell {
  padding-top: 2px;
  padding-bottom: 2px;
}
