@charset "utf-8";

@import "../../../assets/scss/_webfont";
@import "../../../assets/scss/_element-ui";
@import "../../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}

.datePickerInput__popup,
.dateRangePickerInput__popup {
  z-index: 100;
}
























































.date-range-picker-popup__calendar {
  width: 33.3%;

  &:not(:first-child) {
    margin-left: 20px;
  }
}
