@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";
@import "../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}

.datePickerInput__popup,
.dateRangePickerInput__popup {
  z-index: 100;
}








































.date-picker-readonly-input {
  padding: 0 8px;
  width: 100%;
  height: $formPartsHeight - 6;
  border: 1px solid $colorClear;
  border-radius: $sizeRadius;
  background-color: $colorWhite;
  color: $colorText;
  font-size: $basicFontSize;
  line-height: $formPartsHeight - 6;
  transition: background-color 0.3s ease;

  &:not(:disabled):hover {
    background-color: $colorBlue600;
    cursor: pointer;
  }

  &::placeholder {
    color: $colorBase700;
  }

  &:disabled {
    background-color: $colorBase300;
    color: $colorBase600;
  }
}

.date-picker-readonly-input--left {
  text-align-last: left;
}

.date-picker-readonly-input--center {
  text-align: center;
}

.date-picker-readonly-input--focus {
  background-color: $colorBlue600;
  // IE11でplaceholderを表示させ、キャスターを消すために必要
  pointer-events: none;
}
