@charset "utf-8";

@import "../../../assets/scss/_webfont";
@import "../../../assets/scss/_element-ui";
@import "../../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}

.datePickerInput__popup,
.dateRangePickerInput__popup {
  z-index: 100;
}




























































































































































.calendar-date-range-table__inner {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: $datePickerCellHeight;

  &:before,
  &:after {
    position: absolute;
    top: 0;
    z-index: 0;
    height: $datePickerCellHeight;
    background-color: $colorElmentUIBlue100;
    content: "";
  }

  &:after {
    left: 50%;
    width: $datePickerCellHeight;
    border-radius: 50%;
    opacity: 0;
    transform: translateX(-50%);
  }
}

.calendar-date-range-table__inner--desable {
  background-color: $colorElmentUIGray100;
  color: $colorElmentUIGray300;
  pointer-events: none;
}

.calendar-date-range-table__inner--range {
  &:before {
    left: 0;
    width: 100%;
  }
}

.calendar-date-range-table__inner--rangeStart {
  &:before {
    right: 0;
    width: 50%;
  }
  &:after {
    opacity: 1;
  }
}

.calendar-date-range-table__inner--rangeEnd {
  &:before {
    left: 0;
    width: 50%;
  }
  &:after {
    opacity: 1;
  }
}

.calendar-date-range-table__date {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  transition: background-color 0.3s ease;

  &:hover {
    &:not(.calendar-date-range-table__date--selected) {
      color: $colorElmentUIBlue900;
      cursor: pointer;
    }
  }
}

.calendar-date-range-table__date--today {
  color: $colorElmentUIBlue900;
  font-weight: bold;
}

.calendar-date-range-table__date--selected {
  background-color: $colorElmentUIBlue900;
  color: $colorWhite;
  cursor: pointer;
}

.calendar-date-range-table__date--anotherMonth {
  color: $colorElmentUIGray300;
}
