@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";
@import "../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}

.datePickerInput__popup,
.dateRangePickerInput__popup {
  z-index: 100;
}
















































































































.periodChoices {
  display: flex;
}

.periodChoices__item {
  position: relative;
  display: block;

  &:not(:first-child) {
    margin-left: 5px;
  }
}

.periodChoices__radio {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  opacity: 0;
}

.periodChoices__button {
  display: block;
  padding: 5px;
  min-width: 85px;
  border: 1px solid $colorBase400;
  border-radius: 5px;
  background-color: $colorWhite;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s ease;

  &:hover {
    background-color: $colorBlue600;
  }
}

.periodChoices__radio:checked + .periodChoices__button {
  background-color: $colorBlue600;
  font-weight: bold;
  cursor: text;
}
