@charset "utf-8";

@import "../../../assets/scss/_webfont";
@import "../../../assets/scss/_element-ui";
@import "../../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}

.datePickerInput__popup,
.dateRangePickerInput__popup {
  z-index: 100;
}















































































.datePicker-popup-container {
  position: relative;
  background-color: $colorWhite;
}

.datePicker-popup-container__previousButtonContainer {
  position: absolute;
  top: 3px;
  left: 0;
  display: flex;
}

.datePicker-popup-container__nextButtonContainer {
  position: absolute;
  top: 3px;
  right: 0;
  display: flex;
}

.datePicker-popup-container__monthButton {
  position: relative;
  width: 18px;
  height: 18px;
  background-color: $colorWhite;
  line-height: 1;
  cursor: pointer;
  transition: border-color 0.3s ease, background-color 0.3s ease;

  &::before {
    position: absolute;
    top: 9px;
    width: 6px;
    height: 6px;
    border-top: 1px solid $colorElmentUIGray600;
    content: "";
    transition: border-color 0.3s ease;
  }

  &:hover {
    &::before {
      border-color: $colorElmentUIBlue900;
    }
  }
}

.datePicker-popup-container__previousMonthButton {
  &::before {
    left: 10px;
    border-left: 1px solid $colorElmentUIGray600;
    transform: translate(-50%, -50%) rotate(-45deg);
  }
}

.datePicker-popup-container__nextMonthButton {
  &::before {
    right: 10px;
    border-right: 1px solid $colorElmentUIGray600;
    transform: translate(50%, -50%) rotate(45deg);
  }
}

.datePicker-popup-container__yearButton {
  position: relative;
  width: 18px;
  height: 18px;
  background-color: $colorWhite;
  line-height: 1;
  cursor: pointer;
  transition: border-color 0.3s ease, background-color 0.3s ease;

  &::before,
  &::after {
    position: absolute;
    top: 9px;
    width: 6px;
    height: 6px;
    border-top: 1px solid $colorElmentUIGray600;
    content: "";
    transition: border-color 0.3s ease;
  }

  &:hover {
    &::before,
    &::after {
      border-color: $colorElmentUIBlue900;
    }
  }
}

.datePicker-popup-container__previousYearButton {
  margin-right: 12px;

  &::before,
  &::after {
    border-left: 1px solid $colorElmentUIGray600;
    transform: translate(-50%, -50%) rotate(-45deg);
  }

  &::before {
    left: 9px;
  }
  &::after {
    left: 12px;
  }
}

.datePicker-popup-container__nextYearButton {
  margin-left: 12px;

  &::before,
  &::after {
    border-right: 1px solid $colorElmentUIGray600;
    transform: translate(50%, -50%) rotate(45deg);
  }

  &::before {
    right: 9px;
  }
  &::after {
    right: 12px;
  }
}

.datePicker-popup-container__body {
  display: flex;
  align-items: center;
}
