@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";
@import "../../assets/scss/_variable";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: $colorBase900;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}

.datePickerInput__popup,
.dateRangePickerInput__popup {
  z-index: 100;
}

























































































































































































































































































































































































































































































.dateRangePickerInput {
  position: relative;
}

.dateRangePickerInput__container {
  display: flex;
  align-items: center;
  padding: 2px;
  height: $formPartsHeight;
  border: 1px solid $colorBase700;
  border-radius: $sizeRadius;
  background-color: $colorWhite;

  &--foucs {
    border-color: $colorBlue800;
  }
}

.dateRangePickerInput__icon {
  padding-left: 3px;
  width: 25px;
  text-align: center;
}

.dateRangePickerInput__field {
  flex: 1;
}

.dateRangePickerInput__hyphen {
  padding: 0 10px;
}

.dateRangePickerInput__deleteButton {
  margin-right: 5px;
  margin-left: 5px;
  padding: 1px 5px 0;
  height: 18px;
  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }
}

.dateRangePickerInput__popup {
  position: fixed;
  width: 100%;
  border: 1px solid $colorBase400;
  border-radius: $sizeRadius;
  background-color: $colorWhite;
  box-shadow: 0 1px 4px $colorShadow100;
}

.dateRangePickerInput__header {
  padding: 10px;
  border-bottom: 1px solid $colorBase400;
}

.dateRangePickerInput__body {
  padding: 7px 10px 10px;
}

.datePickerPopup-enter-active,
.datePickerPopup-leave-active {
  margin-top: 0;
  opacity: 1;
  transition: opacity 0.3s ease, margin-top 0.3s ease;
}
.datePickerPopup-enter,
.datePickerPopup-leave-to {
  margin-top: -40px;
  opacity: 0;
}
